ks-content-breadcrumbs {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 10px 0;
}

ks-content-breadcrumbs > * {
    position: relative;
    padding: 2px 10px;
    background-color: var(--breadcrumb-color);
    margin: 2px 4px 2px 5px;
    display: inline-block;

    height: 22px;
    box-sizing: border-box;
    outline: none;

    transition: background-color 0.3s ease;
}

ks-content-breadcrumbs > *:before,
ks-content-breadcrumbs > *:after {
    content: "";
    position: absolute;
    top: 0;
    border: 0 solid var(--breadcrumb-color);
    border-width: 11px 5px;
    width: 0;
    height: 0;
    border-left-color: transparent;
    height: -webkit-fill-available;

    transition: border-color 0.3s ease,
                border-left-color 0.3s ease;
}

ks-content-breadcrumbs > *:before {
    border-left-color: transparent;
    margin-left: -15px;
}

ks-content-breadcrumbs > *:after {
    border-color: transparent;
    border-left-color: var(--breadcrumb-color);
    margin-left: 10px;
}

ks-content-breadcrumbs > *:first-child:before {
    border:none;
}

ks-content-breadcrumbs > *:last-child:after {
    border:none;
}

ks-content-breadcrumbs * {
    color: var(--breadcrumb-text-color) !important;
    text-decoration: none !important;
    line-height: 19px;
}

ks-content-breadcrumbs > *:hover,
ks-content-breadcrumbs > *:focus {
    background-color: var(--breadcrumb-color-hover);
}

ks-content-breadcrumbs > *:hover:before,
ks-content-breadcrumbs > *:focus:before {
    border-color: var(--breadcrumb-color-hover);
    border-left-color: transparent;
}

ks-content-breadcrumbs > *:hover:after,
ks-content-breadcrumbs > *:focus:after {
    border-color: transparent;
    border-left-color: var(--breadcrumb-color-hover);
}