import { Component, Prop, h } from '@stencil/core';
import { NameLinkData } from '../../../global/data/listing';

@Component({
  	tag: 'ks-content-breadcrumbs',
	styleUrl: 'breadcrumbs.css'
})
export class ContentBreadcrumbs {
	@Prop() items: NameLinkData[];

	render() {
		return this.items.map(crumb =>
			<a href={crumb.link}>{crumb.name}</a>
		)
	}
}
