import { Component, h, Prop } from '@stencil/core';
import { NameLinkData } from "../../../global/data/listing";

@Component({
  tag: 'ks-content-tags',
  styleUrl: 'tags.css'
})
export class ContentTags {
    @Prop() items: NameLinkData[];
    @Prop({reflect: true}) muted: boolean = false;

    render() {
        return this.items.map(category =>
            <a href={category.link}>{category.name}</a>
        );
    }
}
