ks-content-tags {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    
    max-width: 1200px;
    margin: auto;
}

ks-content-tags > * {
    display: inline-block;
    padding: 3px 10px;
    margin: 2px;
    margin-bottom: 3px;
    line-height: 1.5;
    background: #222222;
    color: #ffffff !important;

    vertical-align: middle;
    white-space: nowrap;
    border-radius: 2px;

    text-decoration: none !important;
}

ks-content-tags[muted] > * {
    background-color: #555555;
}